  .ml11 .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.1em;
    padding-right: 0.05em;
    padding-bottom: 0.15em;
  }

  .ml11 .line {
    opacity: 0;
    position: absolute;
    left: 0;
    height: 100%;
    width: 3px;
    background-color: #fff;
    transform-origin: 0 50%;
  }

  .ml11 .line1 {
    top: 0;
    left: 0;
  }

  .ml11 .letter {
    display: inline-block;
    line-height: 1em;
  }

  .underline span {
    width: 15px;
    height: 100%;
    background: #fff;
    border-radius: 3px;
    position: absolute;
    top: 0;
    left: 10px;
    animation: moving 2s linear infinite;
  }

  @keyframes moving {
    0% {
      left: -20px;
    }

    100% {
      left: 100px;
    }
  }


  @media (max-width:700px) {
    footer {
      bottom: unset;
    }

    .col {
      flex-basis: 100%;

    }

    .col:nth-child(2),
    .col:nth-child(3) {
      flex-basis: 100%;

    }
  }

  .college {
    color: #ffffff;
    padding-left: 0px;
    font-family: "Poppins", sans-serif;
  }



  .college-name {
    font-size: 20px;
  }